.section {
  &-budget {
    &__nav {
      display: flex;
      justify-content: space-between;
      a {
        display: flex;
        @include size-lg {
          border: 1px solid $greyLight;
          width: 100%;
        }
      }
      span {
        &:not(.btn) {
          display: none;
          font-size: $text-sm;
          font-weight: bold;
          @include size-lg {
            border-left: 1px solid $greyLight;
            padding: spacer(4) 0 spacer(4) spacer(4);
            @include center;
          }
        }
      }
      @include size-lg {
        flex-direction: column;
        li {
          &:not(:last-of-type) {
            margin-bottom: spacer(3);
          }
          &:hover {
            background-color: $greyLight;
          }
          &:active {
            background-color: $grey;
            color: $white;
          }
        }
        .btn--square {
          border-radius: 0;
        }
      }
    }
  }
}
