.floating {
  &-nav {
    --height: 0;
    background: $white;
    overflow: hidden;
    font-size: $text-sm;
    line-height: 1rem;
    font-weight: $bold;
    color: $greyDark;
    transition: height 0.4s ease;
    text-align: center;
    @include size(100%, var(--height));

    ul {
      border: 1px solid $greyLight;
    }
    li {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      border-bottom: 1px solid $greyLight;
      max-height: 3rem;
      padding: spacer(4) 0 spacer(4) spacer(4);
      &:hover {
        background-color: $greyLight;
      }
      &:active {
        background-color: $grey;
        color: $white;
      }
      &:last-of-type {
        border-bottom: 0;
        .btn {
          &--square {
            height: calc(3rem);
          }
        }
      }
      .btn {
        &--square {
          border-left: 1px solid $greyLight;
          border-radius: 0;
          margin-left: spacer(4);
          @include size(3rem, calc(3rem - 1px));
        }
      }
    }
  }
  &-box {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 14rem;
    @include fixed($t: calc(var(--headerHeight) + 1rem), $r: -1px);
    #touch {
      position: absolute;
      opacity: 0;
      height: 0;
      + label {
        @extend .btn--floating;
        span {
          transition: transform 0.4s ease;
        }
      }

      &:checked {
        + label {
          span {
            transform: rotate(180deg);
          }
        }
        ~ .floating-nav {
          --height: calc(12rem + 2px);
          &.has-three {
            --height: calc(9rem + 2px);
          }
          &.has-five {
            --height: calc(15rem + 2px);
          }
        }
      }
    }
  }
}
