.main-header {
  z-index: 1;
  position: fixed;
  top: 0;
  margin-bottom: spacer(5);
  background: var(--mainColorBG);
  @include center;
  @include size(100%, var(--headerHeight));
  label[for="menu-button-check"]:nth-of-type(1) {
    z-index: 1000;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    @include size(1.875rem, 1.875rem);
    @include absolute($t: calc(50% - 12px), $r: spacer(3));
    span {
      border-radius: 10px;
      margin: 3px 0;
      background: $white;
      height: 3px;
      transition: 0.4s cubic-bezier(0.68, -0.6, 0.32, 1.6);
      &:nth-of-type(1) {
        width: 100%;
      }
      &:nth-of-type(2) {
        width: 100%;
      }
      &:nth-of-type(3) {
        width: 100%;
      }
    }
    &:hover {
      cursor: pointer;
    }
  }

  input[type="checkbox"] {
    display: none;
    &:checked ~ label {
      span {
        &:nth-of-type(1) {
          transform: rotatez(45deg) translate(6px, 6px);
          transform-origin: bottom;
        }
        &:nth-of-type(2) {
          transform: rotatez(-45deg);
          transform-origin: top;
        }
        &:nth-of-type(3) {
          transform: translate(2px, -15px) rotatez(45deg);
          width: 50%;
          transform-origin: bottom;
        }
      }
    }
  }

  #menu-button-check {
    display: none;
    &:checked {
      ~ .menu {
        right: 0;
        animation: show-menu linear 0.1s;
      }
      ~ label[for="menu-button-check"]:nth-of-type(1) {
        color: $white;
      }
    }
  }

  .menu {
    z-index: 999;
    position: fixed;
    top: 0;
    right: -100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    background: rgba(11, 11, 11, 0.95);
    padding-top: var(--headerHeight);
    animation: hide-menu linear 0.1s;
    @include size(100%, 100%);
    li {
      margin: spacer(5) 0;
      min-width: 200px;
      list-style: none;
      font-size: $text-lg;
      color: $white;
      @include size-md {
        &:hover,
        &:active {
          font-weight: $bold;
        }
        &:active {
          position: relative;
          &:before {
            content: "";
            background: $white;
            @include size(100%, 5px);
            @include absolute($b: -42px, $l: 0);
          }
        }
      }
    }
    a {
      text-decoration: none;
      font-size: inherit;
      color: inherit;
      @include center;
      &:before {
        margin-right: spacer(4);
        align-self: end;
      }
    }
    @include size-md {
      position: relative;
      top: auto;
      right: auto;
      display: inline-block;
      padding-top: 0;
      ul {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        color: $white;
        text-align: right;
        @include size(100%, 100%);
      }
      li {
        display: inline-block;
        font-size: $text-default;
        &:not(:last-of-type) {
          margin: 0 spacer(7) 0 0;
        }
        a {
          &::before {
            display: none;
          }
        }
      }
    }
    @include size-lg {
      li {
        font-size: 1.125rem; //18px
      }
    }
  }

  @include size-md {
    --headerHeight: 104px;
    align-items: flex-start;
    padding: 0 3.5rem;
    label[for="menu-button-check"]:nth-of-type(1) {
      display: none;
    }
  }

  &__logo {
    --logoHeight: 20px;
    --logoWidth: 208px;
    background-image: url(/assets/img/mitsubishi-logo-wide.svg);
    background-size: 100%;
    background-repeat: no-repeat;
    align-self: center;
    @include size(var(--logoWidth), var(--logoHeight));
    @include size-md {
      --logoHeight: 25px;
      --logoWidth: 305px;
    }
  }
}

@keyframes show-menu {
  from {
    right: -100%;
    opacity: 0;
  }

  to {
    right: 0;
    opacity: 1;
  }
}

@keyframes hide-menu {
  from {
    right: 0;
    opacity: 1;
  }

  to {
    right: -100%;
    opacity: 0;
  }
}
