.grid-box-3 {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 3.75rem 1fr 3.75rem;
  grid-template-areas:
    "area-1"
    "area-2"
    "area-3";
  gap: spacer(5) spacer(7);
  @include size-lg {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
    grid-template-areas: "area-1 area-2 area-3";
  }
}
