.main-footer {
  background: $mainColorBG;
  padding: spacer(6) 0;
  color: $white;
  .container {
    display: grid;
    grid-template-columns: 0.66fr 1fr;
    grid-template-rows: repeat(2, 1fr);
    grid-template-areas:
      "logo nav"
      "social social";
    grid-auto-flow: row;
    gap: spacer(7) 0;

    @include size-lg {
      grid-template-columns: repeat(2, auto) 1fr;
      grid-template-rows: 1fr;
      grid-template-areas: "logo nav social";
      gap: 0 4rem;
    }
  }
  &__logo {
    grid-area: logo;
    img {
      width: 5.5rem;
    }
  }

  &__nav {
    display: flex;
    align-items: center;
    line-height: 1rem;
    grid-area: nav;
    li {
      &:not(:last-of-type) {
        margin-bottom: spacer(4);
      }
    }
    .link {
      text-decoration: none;
      font-size: $text-sm;
      font-weight: $bold;
      color: $white;
    }
  }

  &__social {
    display: flex;
    flex-direction: column;
    font-size: $text-sm;
    line-height: $line-height-md;
    font-weight: $bold;
    color: $red;
    grid-area: social;
    .social-list {
      display: flex;
      align-items: center;
      font-size: $text-icon-md;
      color: $white;
      li {
        &:not(:last-of-type) {
          margin-right: spacer(4);
        }
      }
    }

    @include size-lg {
      justify-self: flex-end;
    }
  }
}
