.section {
  &-download {
    &__app {
      --bgHeight: 10rem;
      position: relative;
      background-size: contain;
      background-repeat: no-repeat;
      min-height: var(--bgHeight);
      padding-top: var(--bgHeight);
      .color-box {
        z-index: 1;
        @include size-md {
          margin: 0 spacer(5);
        }
        @include size-lg {
          margin-top: spacer(2);
          max-width: 28.5rem;
        }
      }
      &:before {
        --bgHeight: 17.5rem;
        z-index: -1;
        content: "";
        background-image: url("/assets/img/app-bg.jpg");
        background-size: 100%;
        background-repeat: no-repeat;
        min-height: var(--bgHeight);
        width: 100vw;
        @include absolute($t: 0, $l: -1.5rem);
        @include size-sm {
          left: 0;
          background-size: cover;
          width: 100%;
        }
        @include size-lg {
          margin-top: spacer(2);
          background-size: contain;
        }
      }
      @include size-lg {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        padding-top: 0;
      }
    }
    .container {
      display: grid;
      grid-template-columns: 100%;
      grid-template-rows: 2rem repeat(2, auto);
      grid-template-areas:
        "area-1"
        "area-2"
        "area-3";
      gap: spacer(5) spacer(7);
    }
    @include size-lg {
      .container {
        grid-template-columns: 0.4fr 0.6fr;
        grid-template-rows: 2rem 1fr;
        grid-template-areas:
          "area-1 area-3"
          "area-2 area-3";
      }
    }
  }
}
