//$$ placeholder
@mixin placeholder() {
  &::input-placeholder {
    @content;
  }

  &:placeholder {
    @content;
  }

  &:input-placeholder {
    @content;
  }

  &[placeholder] {
    @content;
  }
}
// Usage: @include placeholder

