//$$ Font-size
//solo con unidades rem
@mixin font-size($size) {
  font-size: px($size); //Fallback
  font-size: $size;
}
// mirar aadir line height

//Usage: @include font-size(1rem);

//$$ Ellipsis on overflow
@mixin txt-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@mixin txt-ellipsis-lastline {
  overflow: hidden;
  word-wrap: break-word;
  text-overflow: -o-ellipsis-lastline;
}

//Usage: @include txt-ellipsis;
//Usage: @include txt-ellipsis-lastline;

//---------------
//$$ Estructura
//---------------

//$$ Disabled
@mixin disabled {
  cursor: default !important;
  pointer-events: none;
}
//se puede ampliar con colores, opacidad etc...

// @include disabled;

//$$ posicin absoluta
@mixin absolute($t: null, $r: null, $b: null, $l: null) {
  position: absolute;
  top: $t;
  bottom: $b;
  left: $l;
  right: $r;
}

//$$ posicin fija
@mixin fixed($t: null, $r: null, $b: null, $l: null) {
  position: fixed;
  top: $t;
  bottom: $b;
  left: $l;
  right: $r;
}

// @include absolute (2px, 3px, 4px, 5px)
// @include absolute ($t:2px, $l:2px)

//$$ hidden
@mixin hidden {
  visibility: hidden;
  border: 0;
  height: 0;
  width: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
}

// Usage: @include hidden

//$$ center - centrado vertical y horizontal con flexbox

@mixin center {
  display: flex;
  justify-content: center;
  align-items: center;
}

// Usage: @include center

//$$ size - tamao ancho y alto
@mixin size($w: null, $h: null) {
  height: $h;
  width: $w;
}

// CREAR VERSIONES MIN Y MAX

// Usage: @include size(4px, 2px)

//$$ Testing
@mixin testing {
  border: 1px solid red;
  background: blue;
}

//TUNEAR EL CAMBIO DE COLORES

// z-index

@mixin z-index($key) {
  z-index: z-index($key);
}
