.section {
  &-vehicle {
    .title {
      &--main {
        --paddingLeft: #{spacer(7)};
        padding-left: var(--paddingLeft);
        @include size-sm {
          --paddingLeft: 0;
        }
      }
    }
    .container {
      @include size-sm {
        width: 420px;
      }
      @include size-md {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-template-areas:
          "pic car"
          "user budget";
        width: auto;
        gap: spacer(7) spacer(7);
        .pic {
          margin: 0 auto;
          grid-area: pic;
          @include size-lg {
            margin: 0;
          }
        }

        .car {
          grid-area: car;
        }

        .user {
          margin: 0 auto;
          grid-area: user;
          @include size-lg {
            margin: 0;
          }
        }

        .budget {
          grid-area: budget;
        }
      }
      @include size-xl {
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: 1fr;
        grid-template-areas: "pic car user budget";
        grid-gap: 0 spacer(5);
      }
    }
    figure {
      text-align: center;
    }
    .vehicle-info {
      --paddingLeft: #{spacer(7)};
      display: inline-block;
      padding-left: var(--paddingLeft);
      line-height: $line-height-md;
      text-align: left;
      span {
        display: block;
      }
      @include size-sm {
        --paddingLeft: 0;
      }
      @include size-md {
        align-self: center;
      }
      @include size-lg {
        align-self: flex-start;
      }
    }
  }
}
