.info-block {
  display: grid;
  grid-template-columns: 1.5rem 1fr;
  grid-template-rows: 1fr;
  grid-template-areas: "icon info";
  grid-auto-flow: row;
  text-align: left;
  gap: 0 spacer(4);

  .info-area {
    line-height: 1.125rem;
    grid-area: info;
  }

  .icon-area {
    grid-area: icon;
    @include size(100%, 100%);
  }
  .title {
    font-size: $text-default;
    line-height: $line-height-md;
    font-weight: $bold;
  }
}
