.shadow-box {
  border-radius: 0.875rem;
  margin: 0 spacer(3);
  width: calc(100% - 1.5rem);
  box-shadow: $shadowBox;
  @include size-lg {
    margin-right: auto;
    margin-left: auto;
    max-width: 960px;
    width: 100%;
    padding: 0;
  }
  @include size-xl {
    max-width: 1140px;
  }
  @include size-xxl {
    max-width: 1320px;
  }
}
