.budget-block {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1.25rem repeat(2, 1rem) 1.25rem;
  grid-template-areas:
    "area-1 area-1 area-1"
    "area-2 area-2 area-2"
    "area-3 area-5 area-7"
    "area-4 area-6 area-8";
  border-bottom: 1px solid $greyLight;
  background-color: $white;
  padding: spacer(3) spacer(5);
  font-size: $text-sm;
  color: $black;
  gap: spacer(1) 0;
  @include size(100%, auto);
  @include size-lg {
    grid-template-columns: 33% repeat(6, 1fr);
    grid-template-rows: 1.5rem 1rem;
    grid-template-areas:
      "area-1 area-4 area-6 area-8 area-9 area-10 area-11"
      "area-2 area-4 area-6 area-8 area-9 area-10 area-11";
    padding: spacer(3) spacer(5);
    gap: spacer(1) spacer(5);
    .flex-span {
      @include center;
    }
  }

  &.has-not-code {
    grid-template-areas:
      "area-1 area-1 area-1"
      "area-1 area-1 area-1"
      "area-3 area-5 area-7"
      "area-4 area-6 area-8";
    > .product-code {
      display: none;
    }
    @include size-lg {
      grid-template-areas:
        "area-1 area-4 area-6 area-8 area-9 area-10 area-11"
        "area-1 area-4 area-6 area-8 area-9 area-10 area-11";
    }
  }
  &__header {
    display: none;
    @include size-lg {
      display: grid;
      grid-template-columns: 33% repeat(6, 1fr);
      grid-template-rows: 3.5rem;
      grid-template-areas: "area-1 area-2 area-3 area-4 area-5 area-6 area-7";
      border-radius: 0.875rem 0.875rem 0 0;
      background: $grey;
      padding: 0 spacer(5);
      font-weight: $bold;
      color: $white;
      gap: 0 spacer(5);
      span {
        display: flex;
        align-items: center;
        &:not(:first-of-type) {
          justify-content: center;
          text-align: center;
        }
      }
    }
  }
  &__total {
    border-radius: 0 0 0.875rem 0.875rem;
    background-color: $grey;
    padding: spacer(6) spacer(5);
    color: $white;
  }
  .product {
    &-title {
      font-size: 1.125rem;
      line-height: 1.5rem;
      font-weight: $bold;
      align-self: center;
    }
    &-code {
      display: block;
      font-size: $text-xs;
    }
    &-info {
      font-weight: $bold;
      @include size-lg {
        @include center;
      }
      &.is-mobile-hidden {
        display: none;
        @include size-lg {
          display: flex;
        }
      }
    }
  }
}
