.section {
  &-opening {
    --mb: #{spacer(6)};
    --bgHeight: 10rem;
    --difDistance: calc(var(--bgHeight) - 0.5rem);
    --imageBG: url("img/opening-bg-sm.png");
    margin-bottom: var(--mb);
    background-image: var(--imageBG);
    background-size: contain;
    background-repeat: no-repeat;
    min-height: var(--bgHeight);
    .container {
      padding-top: var(--difDistance);
      @include size-lg {
        position: relative;
      }
    }
    .color-box {
      @include size-sm {
        --bottom: -2rem;
        --left: calc(50% - 13rem);
        max-width: 26rem; //400px
        @include absolute($b: var(--bottom), $l: var(--left));
      }
      @include size-lg {
        --bottom: -2rem;
        --left: 0;
      }
    }
    @include size-sm {
      --imageBG: url("img/opening-bg.png");
      --difDistance: var(--bgHeight);
      --bgHeight: 384px;
      --mb: 4rem;
      position: relative;
      background-position: top center;
      background-size: cover;
      padding-top: 0;
    }
  }
}
