$green: #b9f0b4;
$greyLight: #c4c4c4;
$grey: #676767;
$greyDark: #0b0b0b;
$red: #ed1c24;
$white: #ffffff;
$black: #000000;

// cambian color bg caja flecha, bg del header y color de texto en el bg

$mainColor: $white;
$mainColorBG: $greyDark;

$buttonPrimary: #00843d;
$buttonPrimaryOver: $green;
$color-error: $red;
$bg-color: $grey;
$colorLink: $buttonPrimary;
$colorLinkOver: $buttonPrimaryOver;
$color-border: $grey;

$shadowFloatingDown: 0 3px 14px rgba(0, 0, 0, 0.25);
$shadowFloatingUp: 0 -3px 3px 0 rgba(0, 0, 0, 0.25);
$shadowModal: 0 3px 32px 0 rgba(0, 0, 0, 0.36);
$shadowFloatingBlur: 0 3px 20px 0 rgba(0, 0, 0, 0.25);
$shadowBox: 0px 3px 14px rgba(0, 0, 0, 0.25);

$utility-colors: (
  "white": $white,
  "black": $black,
  "inherit": inherit,
);

$utility-colors-bg: (
  "main": $bg-color,
  "inherit": inherit,
);
