.btn {
  font-size: $text-default;
  &--budget {
    border: 1px solid $black;
    padding: spacer(4) spacer(6);
    font-weight: $bold;
    @include center;
    &.has-icon {
      &:before {
        margin-right: spacer(5);
        @include size(1.5rem, 1.5rem);
      }
    }
  }
  &--floating {
    border: 1px solid $white;
    background: $red;
    font-size: $text-icon-md;
    color: $white;
    @include size(3rem, 3rem);
    @include center;
  }
  &--square {
    border-radius: 0.3rem;
    background-color: $grey;
    color: $white;
    @include size(3.75rem, 3.75rem);
    @include center;
    &.has-icon {
      &:before {
        font-size: $text-icon-md;
      }
    }
  }

  &--download {
    --buttonHeight: 3.25rem;
    --bgColor: #{$black};
    --textColor: #{$white};
    --borderColor: #{$black};
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border: 2px solid var(--borderColor);
    background: var(--bgColor);
    height: var(--buttonHeight);
    box-sizing: border-box;
    padding: 0 spacer(7) 0 spacer(6);
    font-weight: $bold;
    color: var(--textColor);
    [class^="icon"] {
      flex-shrink: 0;
      margin-right: spacer(4);
      font-size: $text-icon-md;
    }
    &:before {
      content: "";
      background: $white;
      color: $black;
      @include absolute($t: 0, $r: 0);
      @include size(
        calc(var(--buttonHeight) - 0.25rem),
        calc(var(--buttonHeight) - 0.25rem)
      );
    }

    &:after {
      content: "";
      font-size: $text-icon-md;
      color: $black;
      @extend .icon-download;
      @include absolute($t: calc(50% - 10px), $r: 13px);
    }

    &:hover {
      --bgColor: #{$greyLight};
      --textColor: #{$black};
      --borderColor: #{$greyLight};
    }
    &:active {
      --bgColor: #{$white};
      --borderColor: #{$greyLight};
      &:before {
        border-left: 2px solid var(--borderColor);
        box-sizing: content-box;
      }
    }
  }
}
