$font_path: "fonts/montserrat/";

//Old browsers

@font-face {
  font-family: "Montserrat";
  font-weight: 400;
  src: url($font_path + "montserrat-400.woff2") format("woff2"),
    url($font_path + "montserrat-400.woff") format("woff"),
    url($font_path + "montserrat-400.ttf") format("truetype");
}

//Variable Font

@supports (font-variation-settings: normal) {
  @font-face {
    font-family: "Montserrat";
    font-weight: 100 900;
    font-stretch: 50% 200%;
    src: url($font_path + "Montserrat-VF.woff2")
        format("woff2 supports variations"),
      url($font_path + "Montserrat-VF.woff2") format("woff2-variations");
  }
}
