  @media (min-width: 768px) {
    :root {
      --headerHeight: 104px; } }

html {
  height: 100%;
  font-family: "Montserrat", sans-serif;
  font-size: 1rem;
  scrollbar-width: none; }
  html * {
    outline: 0;
    border: 0 none;
    margin: 0;
    padding: 0;
    vertical-align: baseline;
    font-style: inherit;
    font-size: 100%;
