:root {
  --mainColorBG: #{$mainColorBG};
  --headerHeight: 66px;
  @include size-md {
    --headerHeight: 104px;
  }
}

html {
  height: 100%;
  font-family: $font-main;
  font-size: $text-default;
  scrollbar-width: none;

  * {
    outline: 0;
    border: 0 none;
    margin: 0;
    padding: 0;
    vertical-align: baseline;
    font-style: inherit;
    font-size: 100%;
    font-weight: inherit;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0;
    margin-inline-end: 0;
    padding-block-start: 0;
    padding-block-end: 0;
    padding-inline-start: 0;
    padding-inline-end: 0;
  }
}

/* RESET DEFAULT BUTTONS STYLES */
[role="button"],
input[type="submit"],
input[type="reset"],
input[type="button"],
button {
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}

/* Reset `button` and button-style `input` default styles */
input[type="submit"],
input[type="reset"],
input[type="button"],
button {
  border: 0;
  background: none;
  overflow: visible;
  padding: 0;
  font: inherit;
  line-height: normal;
  color: inherit;
  /* cursor: default; */
  cursor: pointer;
  -webkit-user-select: none;
  /* for button */
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-appearance: button;
  /* for input */

  &:focus {
    outline: none;
  }
}

input::-moz-focus-inner,
button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

//REMOVE ARROWS INPUTS
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  margin: 0;
  -webkit-appearance: none;
}

input[type="number"] {
  -moz-appearance: textfield;
  /* Firefox */
}

/* Make `a` like a button */
[role="button"] {
  //cursor: default;
  display: inline-block;
  white-space: pre;
  text-decoration: none;
  color: inherit;
  text-align: center;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

//ALL LISTS
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

//text
b,
strong {
  font-weight: $bold;
}

i {
  display: inline-flex;
  align-items: center;
}

p {
  margin: 0;
  &.is-chain {
    &:not(:last-of-type) {
      margin-bottom: spacer(4);
    }
  }
}

.container,
%container {
  --bs-gutter-x: 1.5rem;
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  padding-right: var(--bs-gutter-x, 0.75rem);
  padding-left: var(--bs-gutter-x, 0.75rem);
  @include size-sm {
    max-width: 540px;
  }

  @include size-md {
    max-width: 720px;
  }

  @include size-lg {
    max-width: 960px;
  }

  @include size-xl {
    max-width: 1140px;
  }

  @include size-xxl {
    max-width: 1320px;
  }
}

@media (prefers-reduced-motion: no-preference) {
  :root {
    scroll-behavior: smooth;
  }
}

.main {
  &-container {
    margin-top: var(--headerHeight);
  }
}
