.budget-info {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 2rem 2.5rem;
  grid-template-areas:
    "area-1 area-1 area-1"
    "area-2 area-3 area-4";
  gap: 1rem 0;
  @include size-lg {
    grid-template-columns: 50% repeat(3, 1fr);
    grid-template-rows: 1fr;
    grid-template-areas: "area-1 area-2 area-3 area-4";
    gap: 0 spacer(5);
  }
  &__title {
    font-size: $text-title-sm;
    font-weight: $bold;
    @include center;
    @include size-lg {
      justify-content: flex-start;
    }
  }

  .subtotal {
    align-self: end;
  }
  .discount {
    align-self: end;
  }
  .line {
    background-color: $white;
  }
  .total {
    border-left: 1px solid $white;
    text-align: right;
    dd {
      font-weight: $bold;
    }
  }

  dl {
    dt {
      font-size: $text-xs;
      line-height: 1rem;
      font-weight: $light;
    }
    dd {
      font-size: $text-sm;
      line-height: 1.25rem;
      font-weight: $medium;
    }
  }
}
