@each $name, $icon in $icons {
  .icon-#{$name},
  %icon-#{$name} {
    mask-image: #{$icon};
    -webkit-mask-image: #{$icon};
    @extend %icon;
  }
}

//::before

@each $name, $icon in $icons {
  .icon-#{$name}-b,
  %icon-#{$name}-b {
    &::before {
      content: "";
      mask-image: #{$icon};
      -webkit-mask-image: #{$icon};
      @extend %icon;
    }
  }
}

//::after

@each $name, $icon in $icons {
  .icon-#{$name}-a,
  %icon-#{$name}-a {
    &::after {
      content: "";
      mask-image: #{$icon};
      -webkit-mask-image: #{$icon};
      @extend %icon;
    }
  }
}

%icon {
  display: inline-block;
  background-color: currentColor;
  height: 1em;
  width: 1em;
  vertical-align: bottom;
  mask-size: contain;
  mask-repeat: no-repeat;
  -webkit-mask-size: contain;
  -webkit-mask-repeat: no-repeat;
}
