.title {
  --fontSize: inherit;
  --lineHeight: inherit;
  --fontWeight: inherit;
  font-size: var(--fontSize);
  line-height: var(--lineHeight);
  font-weight: var(--fontWeight);
  &--main {
    --fontSize: #{$text-title-md};
    --lineHeight: #{$line-height-lg};
    --fontWeight: #{$bold};
  }
  &--secondary {
    --fontSize: #{$text-default};
    --lineHeight: #{$line-height-md};
    --fontWeight: #{$bold};
  }
}
